html,
body {
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

* {
  --color-blue: #00aeef;
  --color-red: #ee1b24;
}

.side-bar button {
  display: flex;
  padding: 1.4rem;
  position: relative;
}
.side-bar button i {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
.side-bar button,
.side-bar li a {
  color: #fff;
  border-radius: 0.5rem;
  background: transparent;
  transition: all 0.3s ease;
}
.side-bar button:hover,
.side-bar li a:hover {
  color: #000;
  background: #fff;
}
.side-bar button:hover .info,
.side-bar li a:hover .info {
  display: inline-block;
}
.side-bar li {
  display: flex;
}
.side-bar li a {
  padding: 1.4rem;
  position: relative;
}
.side-bar li a i {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
.side-bar .logout button {
  padding: 1.4rem;
  position: relative;
  color: #fff;
  border-radius: 0.5rem;
  background: transparent;
  transition: all 0.3s ease;
}
.side-bar .logout button:hover {
  color: #000;
  background: #fff;
}
.side-bar .logout button:hover .info {
  display: inline-block;
}
.side-bar .logout button i {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.robot-name span {
  text-shadow: 0 0 0.2rem #fff;
}

.win {
  font-weight: light;
  font-size: 1.2em;
}/*# sourceMappingURL=index.css.map */