html,
body {
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

$color-blue: #00aeef;
$color-red: #ee1b24;

* {
    --color-blue: #00aeef;
    --color-red: #ee1b24;
}

.side-bar {

    button{
        display: flex;
        padding: 1.4rem;
        position: relative;

        i {
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate(-50%, -50%);
        }
    }
    button,
    li a {
        color: #fff;
        border-radius: .5rem;
        background: transparent;
        transition: all .3s ease;

        &:hover {
            color: #000;
            background: #fff;
        }
        &:hover .info{
            display: inline-block;
        }
    }

    li {
        display: flex;

        a {
            padding: 1.4rem;
            position: relative;
            i {
                position: absolute;
                left: 50%;
                top: 50%;
                transform: translate(-50%, -50%);
            }
        }
    }

    .logout {
        button {
            padding: 1.4rem;
            position: relative;
            color: #fff;
            border-radius: .5rem;
            background: transparent;
            transition: all .3s ease;
            &:hover {
                color: #000;
                background: #fff;
            }
            &:hover .info{
                display: inline-block;
            }
            i {
                position: absolute;
                left: 50%;
                top: 50%;
                transform: translate(-50%, -50%);
            }
        }
        
    }
}

.robot-name{
    span{
        text-shadow: 0 0 .2rem #fff ;
    }
}

.win{
    font-weight: light;
    font-size: 1.2em;
}